.rcb-antd-tabs-nav::before {
    border-color: #e2e2e2 !important;
}

.rcb-antd-list-item-meta-content {
    width: auto !important;
}

// Fix select inputs in WordPress backend
.rcb-antd-select-single:not(.rcb-antd-select-customize-input) .rcb-antd-select-selector {
    box-sizing: border-box;
}

.rcb-antd-select-item {
    line-height: 2;
}

// Reset https://git.io/JLyJJ
.rcb-antd-input {
    padding: 4px 11px !important;
    line-height: 1.5715 !important;
    min-height: auto !important;
    box-shadow: none !important;
    border-radius: 2px !important;
    border: 1px solid #d9d9d9 !important;
    outline: none !important;
}

.rcb-antd-card-meta-description {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

// Fix select dropdowns and messages in modals
.rcb-antd-select-dropdown, .rcb-antd-message {
    z-index: 105000000;
}

// Fix modal height and body overflow
.rcb-antd-modal-body {
    max-height: calc(100vh - 55px - 200px);
    overflow-y: auto;
}
@ant-prefix: rcb-antd;